import React, { useContext } from 'react'
import Key from './Key';
import { AppContext } from '../App';
function Keyboard() {
    let keys1 = ["", "a", "", "", "", "e", "", "i", "", "", "", "u", ""];
    const keys2 = ["", "b", "d", "f", "", "h", "j", "", "k", "l", "m", "n", ""];
    const keys3 = ["p", "r", "s", "", "t", "", "", "v", "w", "z"];
    const { longChar, disabledLetters, correctLetters, almostLetters } = useContext(AppContext);
    if (longChar) {
        keys1 = ["", "a", "", "", "", "e", "", "i", "", "", "", "u", ""];
    } else {
        keys1 = ["", "a", "", "", "", "e", "", "i", "", "", "", "u", ""];
    }
    return (
        <div className='keyboard'>
            <div className='line1'>{keys1.map((key) => {
                return <Key
                    keyVal={key}
                    state={disabledLetters.includes(key) ? "disabled" : correctLetters.includes(key) ? "correct" : almostLetters.includes(key) ? "almost" : ""} />
            })}</div>
            <div className='line2'>{keys2.map((key) => {
                return <Key keyVal={key}
                    state={disabledLetters.includes(key) ? "disabled" : correctLetters.includes(key) ? "correct" : almostLetters.includes(key) ? "almost" : ""} />
            })}</div >
            <div className='line3'>
                <Key keyVal={"Enter"} bigKey={true} />
                {keys3.map((key) => {
                    return <Key keyVal={key}
                        state={disabledLetters.includes(key) ? "disabled" : correctLetters.includes(key) ? "correct" : almostLetters.includes(key) ? "almost" : ""} />
                })}
                <Key keyVal={""} bigKey={true} />
            </div>

        </div>
    )
}

export default Keyboard